export const getPresetConfigByKey = (key) => {
    const presetConfig = PRESETS_2023.find((item) => item.key.toUpperCase() === key.toUpperCase());
    if (presetConfig) {
        return presetConfig;
    }
    console.warn("Preset not found", key);
    return {
        key,
        title: key.toUpperCase(),
        background: "#333",
        color: "#FFFFFF",
    };
};
